import { createRouter, createWebHashHistory } from "vue-router"
import HomeView from "../views/HomeView.vue"
import AdminView from "../views/admin/AdminView.vue"
import EventsView from "../views/admin/EventsView.vue"
import EventListView from "../views/admin/EventListView.vue"
import EventView from "../views/admin/EventView.vue"
import EventAddView from "../views/admin/EventAddView.vue"
import EventModifyView from "../views/admin/EventModifyView.vue"
import TaskListView from "../views/admin/TaskListView";
import TaskView from "../views/admin/TaskView";
import TaskAddView from "../views/admin/TaskAddView";
import TaskModifyView from "../views/admin/TaskModifyView";

const routes = [
  {
    path: "/",
    component: HomeView
  },
  {
    path: "/admin",
    name: "Admin",
    component: AdminView,
    children: [
      {
        path: "events",
        redirect: "/admin/events/",
        name: "Events",
        component: EventsView,
        children: [
          {
            path: "",
            name: "EventList",
            component: EventListView,
          },
          {
            path: "add",
            name: "EventAdd",
            component: EventAddView,
          },
          {
            path: ":eventId",
            redirect: "/admin/events/:eventId/",
            name: "Event",
            component: EventView,
            children: [
              {
                path: "",
                name: "TaskList",
                component: TaskListView,
              },
              {
                path: "tasks/add",
                name: "TaskAdd",
                component: TaskAddView,
              },
              {
                path: "tasks/:taskId",
                name: "Task",
                component: TaskView,
              },
              {
                path: "tasks/:taskId/modify",
                name: "TaskModify",
                component: TaskModifyView,
              }
            ]
          },
          {
            path: ":eventId/modify",
            name: "EventModify",
            component: EventModifyView,
          },
        ]
      },
    ]
  },
  {
    path: "/about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ "../views/AboutView.vue")
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
